import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
// import { Locales } from 'src/app/models/locales';
// import { LocalesService } from 'src/app/services/locales.service';
import { Usuarios } from 'src/app/models/usuario';
import { UsuarioService } from 'src/app/services/usuario.service';
import { AlertController } from '@ionic/angular';
import * as firebase from 'firebase';


@Component({
  selector: 'app-locales',
  templateUrl: './locales.page.html',
  styleUrls: ['./locales.page.scss'],
})
export class LocalesPage implements OnInit {
  locales = [];
  categoria: string;
  textoBuscar = '';
  condicion: boolean = false;
  idUser;
  public user: Usuarios=new Usuarios();
  
  constructor(private router: Router,
              //private localesService: LocalesService,
              private usuarioService: UsuarioService,
              private alertCtrt: AlertController,
              private activateRoute: ActivatedRoute) { }

  ngOnInit() {
    this.activateRoute.paramMap.subscribe(paramMap => {
      this.categoria = paramMap.get('id');
      this.idUser = localStorage.getItem('userId');
      //this.localesService.getLocales().subscribe(res=> {this.locales = res;this.condicion = this.getDatos()});
      if(localStorage.getItem('userId') != null){
        this.usuarioService.getUsuario(localStorage.getItem('userId')).subscribe(res => {this.user =res;});
      }


      firebase.firestore().collection('Locales').where('CategoriaLocal','==',paramMap.get('id')).onSnapshot(snap =>{
      this.locales = []
        snap.forEach(element => {
          this.locales.push(element.data())
        })
        if(this.locales.length == 0){
          this.condicion = true
        }
        //this.nativeAudio.play('audioWo')
      })


      
    });

    


  }

  validarSesion(){
    if(this.idUser != null){
      this.router.navigate(['/crear-local',this.categoria]);
    }

    else{
      this.iniciarSesion();
    }

  }
  async iniciarSesion() {

    const alert = await this.alertCtrt.create({
      cssClass: 'my-custom-class',
      header: 'No ha iniciado sesión',
      
      message: 'Para crear un local primero debe iniciar sesión.',


      buttons: [
        {
          text: 'Iniciar sesión',
          handler: (data) => {
            this.router.navigate(['/login']);
            
          }
        },
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
           // console.log('Confirm Cancel: blah');
          }
        }
      ]
    });
    await alert.present();
  }




  validarVendedor(){
    if(this.user.Vendedor){
      this.router.navigate(['/crear-local',this.categoria]);

    }else{
      this.serVendedor()

    }
  }

  async serVendedor() {

    const alert = await this.alertCtrt.create({
      cssClass: 'my-custom-class',
      header: 'Su cuenta no es de vendedor',
      
      message: 'Para poder usar esta función, vaya a su perfil y solicite ser vendedor.',


      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
           // console.log('Confirm Cancel: blah');
          }
        }
      ]
    });
    await alert.present();
  }


  getDatos(){
    for(let i= 0; i < this.locales.length; i++){
      if(this.locales[i].CategoriaLocal == this.categoria){     
            return false;      
      }
    }
    return true;
  }

  buscar(event){
    const texto = event.target.value
    this.textoBuscar=texto;
  }

}
